
.heading{
    text-shadow: 5px 5px red;
    font-size: 3rem;
    transition: all 0.5s ease;
}
.heading:hover{
    transform: translateY(-7px);
}

input{
    border: none;
    outline-color: #ff5e62;
    padding: 15px;
    width: 300px;
    font-size: 25px;
    color: #ff5e62;
    font-weight: bold;
    background-color: white;
}
::placeholder{
    color: #ff5e62;
}

.form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}
button{
    margin: 15px;
    background-color: white;
    border: none;
    padding: 12px;
    color: #ff5e62;
    font-weight: 800;
    border-radius: 7%;
}
@keyframes color_change{
    0%{
        background-color: white;
        color:#ff5e62;
    }
    100% {
        background-color: #ff5e62;
        color: white;
    }
}
button:hover{
    animation: forwards color_change 1s ease;
}
.footer-links{
    color: white;
    text-decoration: none;
    font-weight: 600;
}
a:hover{
    text-decoration-line: underline;
}
